.visually-hidden {
  position: absolute;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  margin: -1px;
  overflow: hidden;
}

.music__thumb {
  display: flex;
  justify-content: center;
}

.events__list--cards {
  display: flex;
  flex-wrap: wrap;
  margin-top: -30px;
  margin-left: -15px;

  @include for-size(tablet-up) {
    margin-top: -45px;
    margin-left: -27px;
  }

  @include for-size(desktop-up) {
    margin-top: -50px;
    margin-left: -70px;
  }
}

.events__item {
  text-align: center;
  margin-top: 30px;
  margin-left: 15px;
  flex-basis: calc((100% - 2 * 15px) / 2);

  @include for-size(tablet-up) {
    margin-top: 45px;
    margin-left: 27px;
    flex-basis: calc((100% - 3 * 45px) / 3);
  }

  @include for-size(desktop-up) {
    margin-top: 50px;
    margin-left: 70px;
    flex-basis: calc((100% - 4 * 70px) / 4);
  }
}

.music__thumb {
  margin-bottom: 5px;
}

.music__img-poster {
  width: 128px;
  height: 161px;
  border-top-left-radius: 50px;
  border-bottom-right-radius: 50px;

  @include for-size(tablet-up) {
    width: 180px;
    height: 227px;
    border-top-left-radius: 75px;
    border-bottom-right-radius: 75px;
  }

  @include for-size(desktop-up) {
    width: 250px;
    height: 265px;
  }
}

.events__title {
  font-weight: 700;
  line-height: 1.21;
  font-size: 14px;
  margin-bottom: 5px;
  color: var(--title-color);

  @include for-size(tablet-up) {
    font-size: 16px;
    line-height: 1.25;
  }
}

.events__text {
  line-height: 1.21;
  font-size: 14px;
  margin-bottom: 5px;
  color: var(--main-text-color);

  @include for-size(tablet-up) {
    font-size: 16px;
    line-height: 1.25;
  }
}

.music__link {
  display: flex;
  justify-content: center;
  font-size: 12px;
  font-weight: 600;
  line-height: 1.21;
  color: var(--main-text-color);

  @include for-size(tablet-up) {
    font-size: 14px;
    line-height: 1.21;
  }
}

.material-icons {
  font-size: 14px;
}
